
// import request from '@/utils/request'
import request, { post, get } from '@/utils/request'
import { ouchnCustom } from "@/service"

// 乐学下拉分类
export function GetTreeSelectByType(query){
  return request({
    url:ouchnCustom + '/web/goods/getTreeSelectByType',
    method:'get',
    params: query
  })
}

// 首页banner
export function GetBannerList(query){
  return request({
    url:ouchnCustom + '/device/api/homePage/bannerList',
    method:'get',
    params: query
  })
}

