export const SelectOclexueResource = (data) => post(ouchnCustom + '/device/api/OcLexueHome/selectOclexueResource', data, { disableToken: false })

// 书系列表
export function GetBooksList(data) {
	return request({
		url: ouchnCustom + '/device/api/ocLexueResourceBook/selectOclexueResourceBook',
		method: 'post',
		data
	})
}
// 书系课程，试卷总数
export function selectOcLexueResourceBookCourseById(data) {
	return request({
		url: ouchnCustom + '/device/api/ocLexueResourceBook/selectOcLexueResourceBookCourseById?goodsId='+data,
		method: 'get',
	})
}
