// 更新用户学习时长
export function updateTheLearningTime(data) {
	return request({
		url: ouchnCustom + '/device/read/updateOcDownloadReadingForLuxue',
		contentType:"application/x-www-form-urlencoded",
		token: true,
		method: 'post',
		data
	})
}

